#donation-list{
    min-height: 100vh;
    height: 100%;
    position: fixed;
    width: 0%;
    background-color: rgb(19, 14, 14);
    padding: 15px;
    z-index: 5;
    transition: width .7s;
    animation-name: slidein;
    animation-duration:  .7s;
}


#donation-list.hide {
    animation-name: slideout;
    animation-duration:  .71s;
}


#donation-list .donation-table{
  overflow: scroll;
  width: 100%;
  height: calc(100vh - 100px);
}
/* Extra small devices (phones, 600px and down) */

@media only screen and (min-width: 577px) {
    #donation-list {
        width: 40%;
    }
  }

@media only screen and (max-width: 576px) {
    #donation-list {
        width: 60%;
    }
  }

  @media only screen and (max-width: 400px) {
    #donation-list {
        width: 85%;
    }
  }


  @keyframes slidein {
    from {right: -500px;}
    to {right: 0;}
  }

  @keyframes slideout {
    from {right: 0;}
    to {right: -500px;}
  }

  